@import "media";

$breakpoints: (
    5s: 320px,
    phone: 640px,
    tablet: 768px,
    why: 860px,
    desktop: 1024px,
) !default;

@mixin respond-to($breakpoint) {
    // If the key exists in the map
    @if map-has-key($breakpoints, $breakpoint) {
        // Prints a media query based on the value
        @if ($breakpoint == "desktop") {
            @media (min-width: map-get($breakpoints, $breakpoint)) {
                @content;
            }
        } @else {
            @media (max-width: map-get($breakpoints, $breakpoint)) {
                @content;
            }
        }
    }

    // If the key doesn't exist in the map
    @else {
        @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints)}.";
    }
}

.header {
    background: #fbfbfb;
    .container {
        display: flex;
        align-items: center;
        height: 72px;
        position: relative;
        @include respond-to(phone) {
            height: 62px;
            padding: 0 15px;
        }
        .logo {
            margin-right: 32px;
            transform: translateY(3px);
        }
        .slogan {
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #000000;
            @include respond-to(phone) {
                display: none;
            }
        }
        .menu,
        .btn-menu {
            display: none;
        }
        .btn-menu {
            background-color: transparent;
            @include respond-to(phone) {
                display: block;
            }
        }
        .menu {
            position: absolute;
            display: block;
            right: 15px;
            top: 40px;
            background: #ffffff;
            box-shadow: 0px 10px 36px -12px rgba(0, 0, 0, 0.15);
            border-radius: 4px;
            opacity: 0;
            visibility: hidden;
            &.active {
                opacity: 1;
                visibility: visible;
                transition: 0.3s;
            }
            &__item {
                display: flex;
                align-items: center;
                font-weight: 600;
                font-size: 16px;
                line-height: 24px;
                font-feature-settings: "liga" off;
                color: #000000;
                padding: 4px 8px;
                svg {
                    margin-right: 6px;
                }
                &:first-child {
                    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
                    padding-left: 11px;
                    svg {
                        margin-right: 9px;
                    }
                }
            }
        }
        .btn-get {
            margin-left: 25px;
            svg {
                position: absolute;
                top: 10px;
                left: 16px;
            }
            @include respond-to(phone) {
                display: none;
            }
        }
        .btn-number {
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #000000;
            display: flex;
            align-items: center;
            svg {
                margin-right: 8px;
            }
            @include respond-to(phone) {
                display: none;
            }
        }
    }
}
.btn-blue {
    padding: 8px 16px 8px 44px;
    position: relative;
    background: #1c52a3;
    box-shadow: 0px 10px 36px -12px rgba(33, 131, 223, 0.15);
    border-radius: 4px;
