.modal {
    display: none;
    position: fixed;
    z-index: 100;
    width: 840px;
    background: #ffffff;
    top: 175px;
    left: calc(50% - 420px);
    z-index: 100;
    border-radius: 4px;
    padding-bottom: 80px;
    @include respond-to(phone) {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        transform: none;
        border-radius: 0;
        padding: 0 15px;
    }
    animation-name: show;
    animation-duration: 0.5s;
    @keyframes show {
        0% {
            display: none;
            opacity: 0;
        }
        100% {
            display: block;
            opacity: 1;
        }
    }
    .modal-content.unactive {
        display: none;
    }
    .success-content {
        display: none;
        &.active {
            display: block;
        }
        h3 {
            font-weight: bold;
            font-size: 32px;
            line-height: 48px;
            text-align: center;
            color: #232323;
            margin-top: 295px;
            margin-bottom: 225px;
        }
    }
    &-blackout {
        position: fixed;
        z-index: 99;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.65);
        display: none;
        animation-name: show;
        animation-duration: 0.5s;
    }

    &_steps {
        padding: 65px 118px;
        display: flex;
        justify-content: space-between;
        &-item {
            max-width: 98px;
            flex: 1;
            .step-line {
                width: 100%;
                height: 8px;
                background: #c0c0c0;
                margin-bottom: 20px;
                left: 0;
                background-color: #3D8AFF;
                transform: scaleX(0);
                transition: transform 0.5s;
                transform-origin: left;
            }
            .step-title {
                text-align: center;
                font-weight: 600;
                font-size: 14px;
                line-height: 16px;
                transition: 0.3s;
                color: #c0c0c0;
            }
            &:first-child {
                max-width: 139px;
                .step-line {
                    width: 95%;
                    float: right;
                    border-radius: 8px 0 0 8px;
                }
                .step-title {
                    text-align: left;
                }
            }
            &:last-child {
                max-width: 139px;
                .step-line {
                    width: 95%;
                    float: left;
                    border-radius: 0 8px 8px 0;
                }
                .step-title {
                    text-align: right;
                }
            }
            
            &.active-step {
                .step-line{
                        left: 0;
                        background-color: #3D8AFF;
                        transform-origin: bottom left;
                        transform: scaleX(1);
                   
                    
                }
                .step-title {
                    color: #232323;
                }
            }
        }
    }
    .divider {
        margin: 0 0 43px;
    }
    .close {
        position: absolute;
        right: 0;
        top: 0;
        width: 56px;
        height: 56px;
        background: #1c52a3;
        border-radius: 0px 3px 0px 0px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .back-form {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        width: 56px;
        height: 56px;
        background: #1c52a3;
        border-radius: 3px 0 0px 0px;
        cursor: pointer;
        align-items: center;
        justify-content: center;
        &.active {
            display: flex;
        }
    }

    .form_item {
        display: none;
        &.active-form {
            display: block;
        }
        &-title {
            font-weight: bold;
            font-size: 32px;
            line-height: 48px;
            text-align: center;
            color: #232323;
            max-width: 495px;
            margin: 0 auto;
            margin-bottom: 15px;
