        &.form-step-info {
            .btn-blue {
                width: 520px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 8px 0;
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                margin: 24px 160px 0;
                svg {
                    margin-right: 8px;
                }
            }
        }

        .size_field {
            margin: 0 90px;
            padding: 4px;
            background: #f5f5f5;
            border-radius: 4px;
            display: grid;
            gap: 4px;
            grid-template-columns: repeat(3, 1fr);
            input {
                display: none;
                &:checked + label {
                    background: #1c52a3;
                    color: #ffffff;
                }
            }
            label {
                text-align: center;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                color: #232323;
                padding: 24px 0;
                background: #ffffff;
                border-radius: 4px;
                cursor: pointer;
                &:hover {
                    background: #2063c7;
                    color: #ffffff;
                    transition: 0.5s;
                }
            }
        }
    }
}

@import "datepicker";

.datepicker-wrapper {
    #datepicker-input {
        display: block;
        margin: 0 auto;
    }
    input {
        display: none;
    }
    .flatpickr-calendar {
    }
    .flatpickr-calendar.inline {
        margin: 0 auto;
    }
    .btn-blue {
        width: 520px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 8px 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 24px;
        margin: 30px 160px 0;
    }
}
