    .post-page-main {
    display: flex;
    flex-direction: column;
    align-items: center;
   
    
    img {
        width: 100%;
        height: auto;
        margin-top: 133px;
        margin-bottom: 56px;
      }
    & > * {
      max-width: 730px;
    }
  
    h1 {
      font-weight: bold;
      font-size: 48px;
      line-height: 68px;
      color: #232323;
      margin-bottom: 24px;
    }
    span {
      font-size: 16px;
      line-height: 24px;
      color: #232323;
    }
}

.divider {
    opacity: 0.12;
    border: 1px solid #000000;
    height: 1px;
    width: 100%;
    margin-top: 110px;
  }
  