html {
	box-sizing: border-box;
	font-size: 16px;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	height: 100%;
	position: relative;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

button:focus,
input:focus {
	outline: transparent;
}

h5 {
	font-size: 20px;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.container {
	max-width: 1280px;
	margin: 0 auto;

	@media (max-width: 767px) {
		max-width: 100%;
	}
}
.container2 {
	max-width: 1920px;
	margin: 0 auto;

	@media (max-width: 767px) {
		max-width: 100%;
	}
}
body {
	margin: 0;
	font-family: Poppins;
	font-style: normal;
}
button{
	cursor: pointer;
}
button,
button:active,
button:focus{
	outline: none;
	border: transparent;
}
a{
	text-decoration: none;
}